import React, {useEffect, useState} from 'react';

import styles from "./GifImgDef.module.scss"
import {useLazy} from "../../../hooks/useLazy";

function GifImgDef({className, src, gifSrc, ...attrs}) {
    const [imgSrc, setImgSrc] = useState(src)
    const {isInView, ref} = useLazy(null,true)

    useEffect(() => {
        if (isInView) setImgSrc(gifSrc)
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [isInView])

    return (
        <>
            <img
                ref={ref}
                {...attrs}
                src={imgSrc}
                className={`${styles['gifImgDef']} ${className ? className : ''}`}
                alt="gif animation"
            />
        </>
    )
}

export default GifImgDef;