import React, {useCallback} from 'react';

import SecondaryBtn from "../SecondaryBtn/SecondaryBtn";
import GifImgDef from "../GifImgDef/GifImgDef";
import ImageWebp from "../ImageWebp/ImageWebp";

import styles from "./WrapperWithImg.module.scss"

const  WrapperWithImg = ({
                            isReverse,
                            textBlockSize,
                            isWithBtn,
                            titleText,
                            contentTextArr,
                            onClick,
                            gifPoster,
                            gifImg,
                            img,
                            imgWebp,
                            wrapperClass,
                            isMobileReverse
}) => {
        // eslint-disable-next-line
    const onClickCallBack = useCallback(
        onClick,
        [])

    return (
        <section className={`${styles['wrapperWithImg']} ${textBlockSize === 'md' ? styles['wrapperWithImg_lg'] : styles['wrapperWithImg_sm']}  ${isMobileReverse ? styles['wrapperWithImg_reverseMob'] : ''} ${isReverse ? styles['wrapperWithImg_reverse'] : ''} ${wrapperClass ? wrapperClass : styles['wrapperWithImg_def']} container`}>
            <div className={styles["wrapperWithImg__imgBlock"]}>
                {gifImg && <GifImgDef
                    src={gifPoster}
                    gifSrc={gifImg}
                />}
                {img && <ImageWebp
                    srcSet={imgWebp}
                    src={img}
                    alt={titleText}
                    className={styles["wrapperWithImg__img"]}
                />}
            </div>
            <div className={`${styles["wrapperWithImg__textBlock"]} ${styles[`wrapperWithImg__textBlock_${textBlockSize ? textBlockSize : 'sm'}`]}`}>
                <h3 className={`${styles["wrapperWithImg__title"]} titleDef`}>{titleText}</h3>
                <div className={styles['wrapperWithImg__textWrapper']}>
                    {contentTextArr.map((item,index) => (
                        <p key={index} className={`${styles['wrapperWithImg__contentText']} contentTextDef`}>{item}</p>
                    ))}
                </div>
                {isWithBtn && <SecondaryBtn onClick={onClickCallBack} className={styles['wrapperWithImg__btn']}>Learn More</SecondaryBtn>}
            </div>
        </section>
    );
}

export default WrapperWithImg;